$cmp-prefix: #{$prefix}-radio-group;
$el-container: #{$prefix}-form-element-radio;


.#{$cmp-prefix}-form-item-layout-wrapper{
  display: inline-flex;
  .#{$cmp-prefix} {
    padding-top: space(xs);
  }
}

.#{$cmp-prefix} {
  display: flex;
  flex-wrap: wrap;

  &--vertical {
    flex-direction: column;
  }

  &--horizontal {
    .#{$el-container} {
      margin-right: space(l);
    }
  }
}

@include md {
  .#{$cmp-prefix}-form-item-layout-wrapper{
    .#{$cmp-prefix} {
      padding-top: calc(#{space(xxs)} * 0.81);
    }
    .em-ds-em-ds-label__text {
      font-size: 16px;
      font-weight: font-weight(seminal);
    }
  }

  .#{$cmp-prefix} {
    &--horizontal {
      .#{$el-container} {
        margin-right: calc(#{space(s)} * 0.81);
      }
    }
  }
}