$picker-classname: SingleDatePicker_picker;
$day-picker-classname: DayPicker;
$date-range-selector: #{$prefix}-date-range-selector-wrap-day-picker;

@import './components/helpers/clean';
@import './components/DSDatePickerImpl';
@import './components/DatePickerDay/DatePickerDay';
@import './components/DatePickerNavigation/DatePickerNavigation';
@import './components/DatePickerPicker/DatePickerPicker';

.#{$prefix}-form-item--error{
    .#{$prefix}-datepicker{
        .#{$prefix}-input{
            border-right-color: transparent;
        }
        .#{$prefix}-datepicker-range__picker{
            border-top-color: color(danger, 900);
            border-right-color: color(danger, 900);
            border-bottom-color: color(danger, 900);
        }
    }    
}