$breadcrumb-nav: #{$prefix}-breadcrumb;
$crumb-el: #{$prefix}-breadcrumb__crumb;
$label-el: #{$prefix}-breadcrumb__label;
$separator-el: #{$prefix}-breadcrumb__separator;


$basic-height: space(s);
$basic-space: space(xxs)* 0.5;

.#{$breadcrumb-nav} {
  display: flex;
  align-items: center;
  ol {
    width: 100%;
    padding: 0;
    list-style: none;
    display: flex;
  }
}

.#{$crumb-el} {
  display: inline-flex;
  align-items: center;
  flex-shrink: 20;
  min-width: 30px;
  height: $basic-height;


  &:first-child, &:last-child {
    flex-shrink: 0;
  }

  &:hover {
    flex-shrink: 0;
  }

  + .#{$crumb-el} {
    padding-left: $basic-space;

    &::before {
      color: color(neutral, 600);
      display: inline-block; // Suppress underlining of the separator in modern browsers
      content: "/";
      padding-right: $basic-space;
    }

    &--active:not(.#{$crumb-el}--title) {
      &::after {
        color: color(neutral, 600);
        display: inline-block; // Suppress underlining of the separator in modern browsers
        content: "/";
        padding-left: $basic-space;
      }
    }
  }

  .#{$label-el} {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    padding: 0 $basic-space;
    transition: flex-shrink duration(fast) easing(base);
    line-height: $basic-height;
    font-size: font-size(300);
    color: color(brand-primary, 600);
  }

  &:first-child .#{$label-el} {
    padding-left: 0;
  }

  &--title {
    .#{$label-el} {
      color: color(neutral, 700);
    }
  }

  &--active.#{$crumb-el}--title {
    .#{$label-el} {
      cursor: auto;
      pointer-events: none;
    }
  }
}
