// ********************************************
// container definition
$overlay-container: #{$prefix}-overlay;

// ********************************************
// elements definition

@mixin position-all($position, $value) {
  position: $position;
  top: $value;
  right: $value;
  bottom: $value;
  left: $value;
}

.#{$overlay-container} {
  @include position-all(static, 0);

  &--with-scroll {
    position: fixed;
  }

  &--inline {
    display: inline;
    overflow: visible;
  }
}



